.section-7{
  display: flex;
  flex-wrap: wrap;
  &__lists{
    text-align: left;
    flex-basis: 50%;
  }
  &__name{
    margin-left: 3.5em;
  }
  &__list{
    list-style-type: none;
  }
}

.text-left {
 text-align: right;
  // margin-right:7.5em;
  width: 40%;
  
}
@media (min-width:980px) {
  .section-7__lists{
      flex-basis: 25%;
      font-size: 1.8rem;
      
  }
  .section-7__name{
     margin-left: 4rem;
  }
  section-7__list{
    font-size: 1.4rem;
  }
  .section-7{
    padding: 1rem;
  }
  .section-7 .balls{
    padding: 0;
    margin: 0 auto;
  }
   .text-left{
      text-align: right;
      width: 45%;
  }
}