.section-5{
  text-align: center;
  &__box{
    width:50%;
    text-align: center;
    padding: 2em;
    border-radius: 1.8rem;
    margin:2em auto;
  }
  &__box-1{
    background-color: #FEE3CF80;
  }
  &__box-2{
    background-color:#4F92B6F2;
  }
  &__box-3{
    background-color: #FEE3CF80;
  }
  &__icon{
    margin: auto;
    display: block;
    width: 40%;
  }
}

.arrow{
  display: none;
}

@media (min-width:980px) {
  .section-5 h2{
     font-size: 3.2rem;
  }
  .box{
    display: flex;
   justify-content: center;
   align-items: center;
   margin: auto;
   width: 80%;
   gap: 2rem;
  }
  .section-5__box{
    height: 36rem;  
    width: 36.7rem;
  }
  .section-5__box p{
    // margin-top: 5rem;
    font-size: 1.4rem;
  }
  .section-5__box h3{
    margin-top: 5rem;
    font-size: 2.2rem;
  }
  .section-5__box-lg{
    height: 38rem;
  }
  .arrow{
    display: block;
    justify-content: center;
    display: flex;
    align-items: center;
    gap: 2rem;
    margin-left: 15rem;
  }
  .arrow-1{
      width: 3.8rem;
      height: 3.8rem;
  }
  .arrow-2{
    width: 5.8rem;
    height: 5.8rem;
}
  .section-5__heading{
    display: flex;
    justify-content: center;
    align-items: center;
  }
 }